
import Vue from 'vue';

export default Vue.extend({
    name: "WebFrame",
    mounted() {

    },
    beforeMount() {

    },
    methods: {
        handle_iframe_loaded() {
            setTimeout(() => {
                this.iframe_loaded = true
            }, 250)
        }
    },
    computed: {},
    props: {
        url: String
    },
    components: {},
    data() {
        return {
            iframe_loaded: false,
            loading_word_chars: "loading...".split("")
        }
    }
})
